export default {
    data() {
        return {
            userinfo: {
                userNickname: '',
                userPhone: '',
                userPassword: '',
                userSex: '',
                userInterest: '',
                userAvatar: ''
            },
            userPassword2: '',
            title: 'Hello',
            modalName: null,
            userInterest: [],
            checkbox: [{
                value: '山',
                name: '山',
                checked: false,
                hot: false,
            }, {
                value: '水',
                name: '水',
                checked: false,
                hot: false,
            }, {
                value: '园林',
                name: '园林',
                checked: false,
                hot: false,
            }, {
                value: '人文',
                name: '人文',
                checked: false,
                hot: false,
            }, {
                value: '休闲',
                name: '休闲',
                checked: false,
                hot: false,
            }, {
                value: '地质地貌',
                name: '地质地貌',
                checked: false,
                hot: false,
            }, {
                value: '宗教圣地',
                name: '宗教圣地',
                checked: false,
                hot: false,
            }]
        }
    },
    onLoad() {
    },
    methods: {
        regist() {
            if (this.userinfo.userPhone.length != 11) {

                uni.showToast({
                    title: '请输入正确的手机号',
                    duration: 2000,
                    icon: 'none'
                });
                return
            } else if (this.userInterest.length == 0) {
                uni.showToast({
                    title: '请选择个人爱好哦',
                    duration: 2000,
                    icon: 'none'
                });
                return
            } else if (this.userinfo.userPassword.length < 6) {
                uni.showToast({
                    title: '密码太简单喽',
                    duration: 2000,
                    icon: 'none'
                });
                return
            } else if (this.userPassword2 != this.userinfo.userPassword) {
                uni.showToast({
                    title: '密码不一致',
                    duration: 2000,
                    icon: 'none'
                });
                return
            }
            var _this = this
            var _userinfo = this.userinfo
            var _userInterest = this.userInterest
            uni.login({
                provider: 'weixin',
                success: function (loginRes) {

                    uni.getUserInfo({
                        provider: 'weixin',
                        success: function (infoRes) {
                            _userinfo.userAvatar = infoRes.userInfo.avatarUrl
                            _userinfo.userNickname = infoRes.userInfo.nickName
                            if (infoRes.userInfo.gender == 1) {
                                _userinfo.userSex = '男'
                            } else if (infoRes.userInfo.gender == 2) {
                                _userinfo.userSex = '女'
                            } else {
                                _userinfo.userSex = '未知'
                            }
                            _userinfo.userInterest = _userInterest.join(',')
                            _this.registUserInfo()
                        },
                        fail(err) {
                            console.log(err)
                        }
                    })
                }
            })
        },
        registUserInfo() {
            var _userinfo = this.userinfo
            _userinfo.userPassword = this.$md5(_userinfo.userPassword)
            uni.request({
                url: this.$BASE_HOST + 'user/regist', //仅为示例，并非真实接口地址。
                method: 'POST',
                data: _userinfo,
                success: (res) => {
                    if (res.data.code === 20000) {
                        uni.showToast({
                            title: '注册成功',
                            duration: 2000
                        });
                    } else if (res.data.code = 20010) {
                        uni.showToast({
                            title: '手机号已存在',
                            duration: 2000,
                            icon: 'none'
                        });
                        return
                    } else {
                        uni.showToast({
                            title: '服务异常',
                            duration: 2000,
                            icon: 'none'
                        });
                        return
                    }
                    setTimeout(function () {
                        uni.navigateTo({
                            url: '../login/login'
                        })
                    }, 1000)

                }, fail() {
                    uni.showToast({
                        title: '服务异常',
                        duration: 2000,
                        icon: 'none'
                    });
                }
            });
        },
        showModal(e) {
            this.modalName = e.currentTarget.dataset.target
        },
        hideModal(e) {
            this.modalName = null
        },
        ChooseCheckbox(e) {
            let items = this.checkbox;
            let values = e.currentTarget.dataset.value;
            for (let i = 0, lenI = items.length; i < lenI; ++i) {
                if (items[i].value == values) {
                    items[i].checked = !items[i].checked;
                    break
                }
            }
            if (this.userInterest.indexOf(values) != -1) {
                this.userInterest.splice(this.userInterest.indexOf(values), 1)
            } else {
                this.userInterest.push(values)
            }
        }
    }
}
